import { createSelector } from '@ngrx/store';
import {
    RoomStoreState
}  from '../../../../rooms/src/lib/rooms.types';
import {
    loadLaterThatDayResults,
    loadNextDayResults,
    loadRoomsForResults,
    roomStateSelector,
    loadByIdResults,
    loadDiffSizeResults,
    loadRoomsForResultsMap
} from '../../../../rooms/src/lib/rooms.actions';
import {
    loadHomepageBookingsResults,
    loadUpcomingBookingsResults,
    loadPastBookingsResults,
    loadCancelledBookingsResults,
    loadBookingByIdResults,
    bookingStateSelector
} from '../../../../bookings/src/lib/bookings.actions';
import { Building } from '../organisation/building.class';
import { BuildingStoreState } from '../../../../buildings/src/lib/buildings.types';
import { selectLoadBuildingsSuccess } from '../../../../buildings/src/lib/buildings.actions';
import { Space } from '../spaces/space.class';
import { User } from '../users/user.class';
import { selectCurrentUser } from '../../../../user/src/lib/user.actions';
import { UserStoreState } from '../../../../user/src/lib/user.types';
import { LoadingModel } from '../../../../loading/src/lib/loading.model';
import { SelectOption } from '../../../../ui/src/lib/options/select-option';

import {
    SpaceFilterReason,
    filterSpacesByAppliedFilters,
    FilteredSpaces
} from './booking-filter.utils';
import {
    BookingFormData,
    internalBookingTypeOption,
} from './booking.form.data';
import {
    BookingFormState,
    BookingStoreState,
} from './booking.form.types';
import { DateTZ } from '@mckinsey-converge/date-tz';



export const selectBookingFormsData =
    (state: BookingStoreState | RoomStoreState | BuildingStoreState | UserStoreState) =>
        (state as any).bookingForm as BookingFormState;

export const selectActiveForm = createSelector(selectBookingFormsData, state => {
    // Recreate because serialized form dateTz doesn't have DateTZ class methods
    if (!state.activeForm?.dateTz) {
        return state.activeForm
    }
    const { date, is_local_tz, building_tz} = state.activeForm?.dateTz;
    return {
        ...state.activeForm,
        dateTz: new DateTZ({ date: date.valueOf(), is_local_tz, building_tz }),
    }
});

export const selectBookingType = createSelector(selectActiveForm,
    (form) => form?.bookingType);

export const selectBookingLocations = createSelector(selectActiveForm,
    (form) => (form?.location || []).filter(f => !f.groupHeader));

export const selectIsInternal = createSelector(selectBookingType,
    (type) => type?.value === internalBookingTypeOption.value);

export const selectFormFilters = createSelector(selectBookingFormsData,
    (state) => state.activeFormFilters || []);

const selectRoomResultsFromState = createSelector(roomStateSelector,
    loadRoomsForResults.selectors.model);
    
const selectRoomResultsFromState_map = createSelector(roomStateSelector,
    loadRoomsForResultsMap.selectors.model);

const selectLaterThatDayResultsFromState = createSelector(roomStateSelector,
    loadLaterThatDayResults.selectors.model);
const selectNextDayResultsFromState = createSelector(roomStateSelector,
    loadNextDayResults.selectors.model);
const selectByIdResultsFromState = createSelector(roomStateSelector,
    loadByIdResults.selectors.model);
const selectDiffSizeResultsFromState = createSelector(roomStateSelector,
    loadDiffSizeResults.selectors.model);

export const selectHomepageBookingsResultsFromState = createSelector(bookingStateSelector,
        loadHomepageBookingsResults.selectors.model);
export const selectUpcomingBookingsResultsFromState = createSelector(bookingStateSelector,
    loadUpcomingBookingsResults.selectors.model);
export const selectPastBookingsResultsFromState = createSelector(bookingStateSelector,
        loadPastBookingsResults.selectors.model);
export const selectCancelledBookingsResultsFromState = createSelector(bookingStateSelector,
        loadCancelledBookingsResults.selectors.model);
export const selectBookingByIdResultsFromState = createSelector(bookingStateSelector,
        loadBookingByIdResults.selectors.model);

/**
 * Wraps {@link filterSpacesByAppliedFilters} with {@link LoadingModel} interop.
 * This will only filter data if there is data to filter, and returns a success {@link LoadingModel}.
 */
const filterSpacesByAppliedFiltersIfSuccess = (
    results: LoadingModel<Space[]>,
    formFilters: SelectOption<any>[],
    activeForm: BookingFormData,
    buildings: Building[] | undefined,
    currentUser: User | undefined): LoadingModel<FilteredSpaces> => {
    const data = results.optionalSuccess;

    // don't filter model if no success found
    return !activeForm ? results.mutate({
        spaces: data, // the result here is a raw JSON Space, not the Space class
        reason: SpaceFilterReason.None,
        statusMap: {}
    }) : results.mutate(
        filterSpacesByAppliedFilters(formFilters, data, activeForm, buildings || activeForm.buildings, currentUser));
};

export const selectFilteredResults = createSelector(selectRoomResultsFromState,
    selectFormFilters,
    selectActiveForm,
    selectLoadBuildingsSuccess,
    selectCurrentUser,
    filterSpacesByAppliedFiltersIfSuccess);

export const selectMapFilteredResults = createSelector(selectRoomResultsFromState_map,
    selectActiveForm,
    selectLoadBuildingsSuccess,
    selectCurrentUser,
    (results, activeForm, buildings, currentUser) => filterSpacesByAppliedFiltersIfSuccess(results, [], activeForm, buildings, currentUser));

export const selectFilteredLaterDayResults = createSelector(selectLaterThatDayResultsFromState,
    selectFormFilters,
    selectActiveForm,
    selectLoadBuildingsSuccess,
    selectCurrentUser,
    filterSpacesByAppliedFiltersIfSuccess);

export const selectFilteredNextDayResults = createSelector(selectNextDayResultsFromState,
    selectFormFilters,
    selectActiveForm,
    selectLoadBuildingsSuccess,
    selectCurrentUser,
    filterSpacesByAppliedFiltersIfSuccess);

    
export const selectFilteredByIdResults = createSelector(selectByIdResultsFromState,
    selectActiveForm,
    selectLoadBuildingsSuccess,
    selectCurrentUser,
    (results, activeForm, buildings, currentUser) => filterSpacesByAppliedFiltersIfSuccess(results, [], activeForm, buildings, currentUser));

export const selectFilteredDiffSizeResults = createSelector(selectDiffSizeResultsFromState,
    selectFormFilters,
    selectActiveForm,
    selectLoadBuildingsSuccess,
    selectCurrentUser,
    filterSpacesByAppliedFiltersIfSuccess);
    
/**
 * Maps selected options by loaded buildings.
 */
export const selectSelectedBuildingOptions = createSelector(
    selectBookingLocations,
    selectLoadBuildingsSuccess,
    (locations, buildings) => (locations
        .map(l => buildings?.find(b => b.id === l.value))
        .filter(f => !!f) as Building[])
);

