import {
    Component,
    OnInit,
    Input,
    SimpleChanges,
    EventEmitter,
    Output,
    OnChanges
} from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import {
    debounceTime,
    distinctUntilChanged,
    switchMap,
    catchError,
    map,
    first,
    take,
    withLatestFrom,
} from 'rxjs/operators';
import { BehaviorSubject, Observable, of, Subject } from 'rxjs';

import { BaseDirective } from '@mckinsey-converge/base';
import { ApplicationService, Booking, BookingAction, BookingStateService, ISpaceAvailabilityOptions, mapBuildingToSelectOption, mapCityToAllSelectOption, SeriesAction, toggleSelectedByGroup } from '@mckinsey-converge/data-common';
import { Space } from '@mckinsey-converge/data-common';
import { BookingFlowEvent } from '../booking-modal.component';
import { OrganisationService } from '@mckinsey-converge/data-common';
import { SpacesService } from '@mckinsey-converge/data-common';
import { randomInt } from '@mckinsey-converge/base';;
import { DateTZ } from '@mckinsey-converge/date-tz';
import { SelectOption } from '@mckinsey-converge/ui';


@Component({
    selector: 'booking-find-space',
    templateUrl: './find-space.component.html',
    styleUrls: ['./find-space.component.scss'],
})
export class BookingFindSpaceComponent extends BaseDirective implements OnInit, OnChanges {
    /** Form control for setting the active space */
    @Input() public spaces: FormControl;
    /** Form group for booking */
    @Input() public form: FormGroup;

    @Input() public isCloning: SeriesAction | BookingAction | null;

    @Input() public booking: Booking;
    /** Emitter for flow update events */
    @Output() public event = new EventEmitter<BookingFlowEvent>();

    /** List of spaces available during the required period */
    public space_list: Space[] = [];
    /** List of users from an API search */
    public search_results$: Observable<Space[]>;
    /** Whether space list is loading */
    public loading: boolean;
    /** List of zones to filter the spaces on */
    public zone_ids: string[] = [];
    /** Subject holding the value of the search */
    public readonly change$ = new Subject<string | string[]>();

    public searchQuery: string = '';

    public formGroup = new FormGroup({
        location: new FormControl([]),
    });

    public options = new BehaviorSubject<SelectOption<any>[]>([]);

    public selectedOptions = new BehaviorSubject<SelectOption<any>[]>([]);

    public savedSelection = [];

    public selectedSpace: Array<Space & { unavailable?: boolean }> = [];

    public oldSpace = [];

    /**  Whether multiple spaces are allowed to be selected */
    public get multiple(): boolean {
        return this._service.setting('app.booking.multiple_spaces');
    }

    public get has_valid_space() {
        return this.spaces?.value?.some(_ => _.id && _.bookable);
    }

    public get initialBuildingIds(): string[] {
        return this._org.building ? [this._org.building.id] : [];
    }

    constructor(
        private _service: ApplicationService,
        private _spaces: SpacesService,
        private _org: OrganisationService,
        private _booking_state: BookingStateService,
    ) {
        super();
    }

    public ngOnInit(): void {
        this._spaces.initialised.pipe(first((_) => _)).subscribe(() => {
            let request_id = 0;
            // Listen for input changes
            this.search_results$ = this.change$.pipe(
                debounceTime(400),
                distinctUntilChanged(),
                switchMap((bld = []) => {
                    this.loading = true;
                    request_id = randomInt(9999_9999);
                    const dateTz = this.form.controls.dateTz.value;

                    const ignore = this.form.controls.action?.value === SeriesAction.EDIT ? this.form.controls.id.value : null

                    // R--- need to pass on the all day param and let libs/data-common/src/lib/spaces/spaces.service.ts 
                    // availability do the math.
                    let query: ISpaceAvailabilityOptions = {
                        dateTz: this.form.controls.all_day.value
                        ? dateTz.startOfValue('day').startOfValue('minute').startOfValue('second')
                        : dateTz.startOfValue('minute').startOfValue('second'),
                        duration: this.form.controls.all_day.value
                            ? 24 * 60
                            : this.form.controls.duration.value,
                        zone_ids: bld.length ? bld : this._org.building.id,
                        bookable: true,
                        recurrence_count: this.form.controls.recurrence_count.value,
                        recurrence_period: this.form.controls.recurrence_period.value,
                        recurrence_endTz: this.form.controls.recurrence_endTz.value,
                        recurrence_interval: this.form.controls.recurrence_interval.value,
                        recurrence_starts: this.form.controls.recurrence_starts.value,
                        is_recurrent: this.form.controls.is_recurrent.value,
                        recurrence_days: this.form.controls.recurrence_days.value,
                        ignore
                    };

                    /* istanbul ignore else */
                    if (this.zone_ids && this.zone_ids.length) {
                        query.zone_ids = this.zone_ids.join(',');
                    }
                    const id = request_id;
                    return this._spaces.available(query).then(async (list) => ({ id, list }));
                }),
                catchError((_) => of({ id: request_id, list: [], error: _})),
                map((resp: { id: number, list: Space[]}) => {
                    this.loading = false;
                    return resp.id === request_id ? resp.list : this.space_list;
                })
            );

            // Process API results
            this.subscription(
                'search_results',
                this.search_results$.subscribe((list) => {
                    this.space_list = list.filter((space) => {
                        if (!space.available && !this.form.controls.action?.value) {
                            return false;
                        }

                        for (const zone of this.zone_ids) {
                            if (space.zones.includes(zone)) {
                                return true;
                            }
                        }
                        return !this.zone_ids.length;
                    }).filter(space => space.available);

                    this.space_list.sort((a, b) => this.sort(a, b));
                    if(!this.selectedOptions.value.length){
                        this.setDefaultBuilding()
                    }
                    this.space_list.map((space)=>{
                        if(space.id === this.spaces.value[0]?.id){
                            this.spaces.setValue([space])
                            const spaceConflicts = space.recurrence_availability.filter(recurr => !recurr.available);
                            this.form.controls.recurrence_exceptions.setValue(spaceConflicts.map((recurr)=> recurr.date))
                        }
                    })

                    if (
                        this.isCloning === BookingAction.CLONE
                        || this.isCloning === SeriesAction.CLONE
                    ) {

                        const space = this.space_list.find(space => space.id === this.booking.room.id);
                        const bookable = space !== undefined;
                        this.space_list = bookable && this.space_list.filter(space => space.id !== this.booking.room.id) || this.space_list;

                        const original_space = new Space({
                            ...this.booking.room,
                            ...(space || {
                                settings: {
                                    ...this.booking.room.settings,
                                    recurrence_availability: []
                                }
                            } as any),
                            bookable
                        });

                        this.oldSpace = [original_space];
                        this.spaces.value.some(space => space.id === original_space.id) && this.spaces.setValue([original_space]);
                        this.selectedSpace.push(original_space);
                    }

                    if(this.form.controls.is_recurrent.value){
                        this.sortByRecurrenceAvailability();
                    }

                    const booking_space_list = this.form.controls.space_list.value
                    if(booking_space_list.length !== 0){
                        this.spaces.setValue(booking_space_list)
                    }
                })
            );
            
            this.change$.next('');
        });

        this.subscription('buildings', this._org.listen('buildings').subscribe((bld_list) => {
            const city = this._org.building.city
            const timezone = this._org.building.timezone
            const grouped_by_city = {
                key: {name: city, timezone},
                value: bld_list?.filter((bld)=> bld.city === city) || []
            }
            const city_buildings = grouped_by_city.value.map(
                (bld)=>mapBuildingToSelectOption(grouped_by_city.key.name, bld)
            )
            const buildings  = [
                mapCityToAllSelectOption(grouped_by_city.key),
                ...city_buildings
            ] 
            this.options.next(buildings)
        }));
        
    }

    public ngOnChanges(changes: SimpleChanges): void {
        /* istanbul ignore else */
        if (changes.form) {
            const onChange = (_) => this.change$.next(_);
            /* istanbul ignore else */
            if (this.form.controls.dateTz) {
                this.subscription(
                    'date_field',
                    this.form.controls.dateTz.valueChanges.subscribe(onChange)
                );
            }
            /* istanbul ignore else */
            if (this.form.controls.duration) {
                this.subscription(
                    'duration_field',
                    this.form.controls.duration.valueChanges.subscribe(onChange)
                );
            }
        }
    }

    /**
     * Update the selected space list
     * @param space Space to add/toggle in the selected list
     */
    public selectSpace(space: Space) {
        const list = this.spaces.value;
        if (list.find((item) => item.id === space.id)) {
            this.spaces.setValue(list.filter((item) => item.id !== space.id));
        } else {
            this.spaces.setValue([space]);
        }
    }

    /**
     * Proceed to the next step in the space booking flow
     */
    public next(): void {
        if(this.form.controls.is_recurrent.value && 
            this.form.controls.recurrence_exceptions.value.length){
            const id = this.form.controls.id.value
            if(id && this.form.controls.action?.value === 'edit'){ 
                this.handleEditConflicts(id)
            }else{
                this.event.emit({ type: 'next', step: 'recurrence' });
            }          
        }else{
            if(this.booking && this.booking.occurrence_edits && this.booking.occurrence_edits.length) {
                this.event.emit({ type: 'next', step: 'changes' });
            } 
            else {
                this.event.emit({ type: 'next', step: 'search' });
            }
        }
        
    }

    /** Move flow to previous step */
    public previous(): void {
        this.event.emit({ type: 'previous', step: 'search' });
    }

    /**
     * Compare two spaces to determine order
     * @param space_a
     * @param space_b
     */
    public sort(space_a: Space, space_b: Space) {
        const bld = this._org.buildings.find((bld) => space_a.zones.includes(bld.id));
        const bld_b = this._org.buildings.find((bld) => space_b.zones.includes(bld.id));
        if (bld && bld !== bld_b) {
            return bld.name.localeCompare(bld_b?.name);
        }
        const sort_order = [...bld.sort_order].reverse();
        for (const zone_id of sort_order) {
            if (zone_id === '*') {
                continue;
            }
            const a_has_zone = space_a.zones.indexOf(zone_id) >= 0;
            const b_has_zone = space_b.zones.indexOf(zone_id) >= 0;
            if (a_has_zone && !b_has_zone) {
                return 1;
            } else if (b_has_zone && !a_has_zone) {
                return -1;
            }
        }
        return space_a.name.localeCompare(space_b.name);
    }

    public sortByRecurrenceAvailability() {
        this.space_list.sort((a, b)=>{
            const a_availability = (a.recurrence_availability?.filter(recurr => recurr.available)?.length*100)/a.recurrence_availability.length
            const b_availability = (b.recurrence_availability?.filter(recurr => recurr.available)?.length*100)/b.recurrence_availability.length
            return b_availability - a_availability
        })
    }
    public toggle(option: SelectOption<any>) {
        // update the selected value
        this.selectedOptions.pipe(
            take(1),
            withLatestFrom(this.options),
            map(([selectedOptions, options]) => {
                return toggleSelectedByGroup(options, selectedOptions, option);
            })
        ).subscribe(next => {
            const formData = this.formGroup.value;
            this.formGroup.setValue({
                location: formData.location,
            });
            this.selectedOptions.next(next);
            this.savedSelection = next.map((bld)=>bld.value)     
            
        });
    }

    public setDefaultBuilding(){
        const defaultBld = this.options.value.filter(opt => opt.value === this._org.building.id)
        this.selectedOptions.next(defaultBld)
    }

    public clearList(){
        this.space_list = []
        this.savedSelection = []
    }

    public triggerSearch = () =>{
        if(this.savedSelection.length){
            this.change$.next(this.savedSelection)
        }else{
            this.space_list = []
        }
    }

    public handleEditConflicts = (id: string) =>{
        /** When editing, compare exceptions with the original ocurrence list */
        const recurrence_exceptions = this.form.controls.recurrence_exceptions;
        let occurrences_org: Array<number>
        let booking_org: Booking;

        this._booking_state.bookings.subscribe(bookings => {
            booking_org = bookings.filter((booking) => booking.id === id)[0]
            occurrences_org = booking_org.occurrence_details.map((ocurr)=>
                ocurr.start_epoch
            )
        })

        const { hours, minutes } = new DateTZ({ date: booking_org.start_epoch * 1000, is_local_tz: false, building_tz: booking_org.timezone});
        const { hours: to_hours, minutes: to_minutes } = this.form.controls.dateTz.value;
        const new_conflicts = hours === to_hours && minutes === to_minutes
            ? recurrence_exceptions.value.filter((date: number)=> occurrences_org.includes(date))
            : recurrence_exceptions.value.filter((date: number)=> !occurrences_org.includes(date));

        if(new_conflicts.length){
            recurrence_exceptions.setValue([...new_conflicts])
            this.event.emit({ type: 'next', step: 'recurrence' });
        }else{
            this.event.emit({ type: 'next', step: 'search' });
        }
    }

    // Create a computed property to filter spaces based on the search query
    get filteredSpaces() {
        if (!this.space_list) {
            return [];
        }

        // Filter spaces based on the searchQuery
        return this.space_list.filter((space) => {
            let spaceName = space.simple_name || space.settings?.room_name || space.local_name;
            return spaceName.toLowerCase().includes(this.searchQuery.toLowerCase());
        });
    }

}
