import { Component, Input, Output, EventEmitter, NgZone, ChangeDetectorRef, ChangeDetectionStrategy, OnInit, ViewEncapsulation, SimpleChanges } from '@angular/core';
import { trigger, style, animate, transition } from '@angular/animations';
import { MatDialog } from '@angular/material/dialog';

import { BaseDirective } from '@mckinsey-converge/base';
import { Booking, CateringCategory, CateringItem, SpacesService } from '@mckinsey-converge/data-common';
import { ApplicationService } from '@mckinsey-converge/data-common';
import { CateringOrder } from '@mckinsey-converge/data-common';
import { DialogEvent } from '@mckinsey-converge/base';
import { ConfirmModalComponent } from '@mckinsey-converge/legacy-overlays';
import { MeetingDetailsModalComponent } from 'src/app/calendar/meeting-details-modal/meeting-details-modal.component';
import { BookingsService } from '@mckinsey-converge/data-common';
import { BookingStateService, BOOKING_STATUS } from '@mckinsey-converge/data-common';
import {  formatDateWithSuffix } from '@mckinsey-converge/base';

const STATES: { [id: string]: string } = {};

@Component({
    selector: 'approvals-event',
    templateUrl: './event.component.html',
    styleUrls: ['./event.component.scss'],
		// encapsulation: ViewEncapsulation.None,
    animations: [
        trigger('show', [
            transition(':enter', [
                style({ opacity: 0, height: 0 }),
                animate(200, style({ opacity: 1, height: '*' })),
            ]),
            transition(':leave', [
                style({ opacity: 1, height: '*' }),
                animate(200, style({ opacity: 0, height: 0 })),
            ]),
        ]),
    ],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class DayViewApprovalsEventComponent extends BaseDirective implements OnInit {
    /** Event to display */
    @Input() event: Booking;
    @Input() totalCateringItems: number;
    /** Event emitter for event actions */
    @Output() acting = new EventEmitter<boolean>();

    /** Whether approval action is being processed */
    public loading: boolean;
    /** Whether event details are shown */
    public shown: boolean;
    /* Booking type : Internal, External, Interview... */
    public bookingType: string;
    /* Booking date */
    public date: string;
    /* Booking creation date */
    public creationDate: string;
    /* Office address */
    public officeAddress: string;

    public showCateringNote: boolean = false;

    public showEquipmentNote: boolean = false;

		 /** Whether the meeting has catering attached */
    public has_catering: boolean;
    public cateringNote: string = '';
    public equipmentNote: string = '';
    public cateringOrderItems: number = 0;

   
    /** Type of event */
    public get start(): string {
        return this.event.startDateTz.formatDate('h:mm a').toLocaleLowerCase();
    }

    /** Type of event */
    public get end(): string {
        return this.event.endDateTz.formatDate('h:mm a').toLocaleLowerCase();
    }

    /** View booking details */
    public view() {
        const room_id = Object.keys(this.event.approval_status)[0] || '';
        const space = this._spaces.find(room_id) || this.event.room
        this.timeout(
            'view',
            () => {
                this._dialog.open(MeetingDetailsModalComponent, {
                    data: {
                        space,
                        booking: new Booking({ ...this.event.toApiJSON(), room_id, room: space }),
                    },
                    autoFocus: false,
                    backdropClass: 'mck-modal-backdrop',
                });
                this._service.set('CONCIERGE.day_view.viewing', this.event);
            },
            50
        );
    }

    constructor(
        private _service: ApplicationService,
        private _bookings: BookingsService,
        private _booking_state_sevice: BookingStateService,
        private _dialog: MatDialog,
        private _spaces: SpacesService,
        private _zone: NgZone,
        private cdRef: ChangeDetectorRef,
    ) {
        super();
    }
    ngOnInit(): void {
        this.cateringOrderItems = 0
       this.updateData();
    }

    public updateData() {
        const type = this.event.booking_type;
        this.bookingType = `${type.charAt(0).toUpperCase()}${type.slice(1)}`;
        const catering: readonly CateringOrder[] = this.event.catering;
        this.has_catering = !!catering.find((order) => this.event.space.email === order.location_id);
        this.cateringNote = this.event.notes.filter(el => el.type === 'catering')[0]?.message || '';
        this.equipmentNote = this.event.notes.filter(el => el.type === 'equipment')[0]?.message || '';

        const createdDate = this.event.creationDateTz.formatDate('dd MMM yyyy');
        const createdTime = this.event.creationDateTz.formatDate('h:mm a').toLocaleLowerCase();
        this.creationDate = `${ formatDateWithSuffix(createdDate) },  ${ createdTime }`;
        this.date =  formatDateWithSuffix(this.event.date_string);
        this.officeAddress = `${this.event.room.building?.name} ${this.event.room.level.name} ${this.event.space.building?.city}`;
        this.cdRef.detectChanges();
    }

      
    /**
     * Approve the meeting for the set room
     */
    public accept(event: Event) {
				event.stopPropagation();
        this.acting.emit(true);
        const ref = this._dialog.open(ConfirmModalComponent, {
            data: {
                title: 'Approve Meeting',
                content: `Approve ${this.event.organiser.name}'s meeting in "${this.event.space.name}" at ${this.event.start_time} for ${this.event.length_string} on ${this.event.date_string}`,
                icon: { type: 'icon', class: 'material-icons', content: 'event_available' },
                action: 'Ok',
            },
            autoFocus: false,
      backdropClass: 'mck-modal-backdrop',
        });
        ref.componentInstance.event.subscribe((event: DialogEvent) => {
            /* istanbul ignore else */
            if (event.reason === 'done') {
                this.loading = true;
                const event = this.event.toJSON();
                this._bookings
                    .accept(this.event.id, {
                        concierge: true,
                        host: this.event.organiser.email,
                        room_email: this.event.space?.email,
                        icaluid: this.event.icaluid,
                        start: event.start,
                        end: event.end
                    })
                    .then(
                        (acceptedBooking) => {
                            this._service.notifySuccess('Meeting approved.');
                            this.event.status = BOOKING_STATUS.approved;
                            STATES[this.event.id] = BOOKING_STATUS.approved;
                            this._booking_state_sevice.replace(this.event);
                            this._service.set('BOOKING.states', STATES);
                            this.loading = false;
                            this.acting.emit(false);
                            this.cdRef.detectChanges();
                        },
                        () => {
                            this.acting.emit(false);
                            this._service.notifyError('Error approving meeting.');
                            this.loading = false;
                            this.cdRef.detectChanges();
                        }
                    );
            }
            ref.close();
        });
    }

    /**
     * Decline meeting for the set room
     */
    public decline(event: Event) {
			event.stopPropagation();
        this.acting.emit(true);
        const ref = this._dialog.open(ConfirmModalComponent, {
            data: {
                title: 'Decline Meeting',
                content: `Decline ${
                    this.event.organiser.name || this.event.organiser
                }'s meeting in "${this.event.space.name}" at ${this.event.start_time} for ${
                    this.event.length_string
                } on ${this.event.date_string}`,
                icon: { type: 'icon', class: 'material-icons', content: 'event_busy' },
                action: 'Ok',
            },
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });
        ref.componentInstance.event.subscribe((event: DialogEvent) => {
            /* istanbul ignore else */
            if (event.reason === 'done') {
                this.loading = true;
                const booking = this.event.toJSON();
                this.loading = true;
                this._bookings
                    .decline(booking.id, {
                        booking_id: booking.id,
                        organiser: booking.organiser.email,
                        room_email: this.event.space?.email,
                        icaluid: booking.icaluid,
                        start: booking.start,
                        end: booking.end,
                        
                    })
                    .then(
                        () => {
                            this._service.notifySuccess('Meeting declined.');
                            this.event.status = BOOKING_STATUS.declined;
                            STATES[this.event.id] = BOOKING_STATUS.declined;
                            this._booking_state_sevice.replace(this.event);
                            this._service.set('BOOKING.states', STATES);
                            this.loading = false;
                            this.acting.emit(false);
                            this.cdRef.detectChanges();
                        },
                        () => {
                            this.acting.emit(false);
                            this._service.notifyError('Error declining meeting.');
                            this.loading = false;
                            this.cdRef.detectChanges();
                        }
                    );
            }
            ref.close();
        });
    }


		ngOnChanges(changes: SimpleChanges) {
			// console.log('CHANGES: ',changes);
			this.updateData();
			this.cdRef.detectChanges();
		}
}
