import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { FormGroup } from '@angular/forms';

import { Booking, BookingAction } from '@mckinsey-converge/data-common';
import { ApplicationService } from '@mckinsey-converge/data-common';
import { DialogEvent } from '@mckinsey-converge/base';
import { BaseDirective } from '@mckinsey-converge/base';
import { BookingsService, BOOKING_STATUS } from '@mckinsey-converge/data-common';
import { DeleteConfirmModalComponent } from '../../bookings/delete-confirm-modal/delete-confirm-modal.component';
import { Space } from '@mckinsey-converge/data-common';
import { BookingStateService } from '@mckinsey-converge/data-common';
import { BookingModalComponent } from 'src/app/bookings/booking-modal/booking-modal.component';

export interface MeetingDetailsModalData {
    space: Space;
    booking: Booking;
}

@Component({
    selector: 'a-meeting-details-modal',
    templateUrl: './meeting-details-modal.component.html',
    styleUrls: ['./meeting-details-modal.component.scss'],
})
export class MeetingDetailsModalComponent extends BaseDirective implements OnInit {
    /** Emitter for user action on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();
    /** Display string for the loading state */
    public loading: string;
    /** Handler for request errors */
    private _on_error = (message) => (err) => {
        this._service.notifyError(`${message}. Error: ${err.message || err}`);
        this.setLoading(null);
    };

    /** Booking to view details */
    public get booking(): Booking {
        return this._data.booking;
    }

    public set booking(booking: Booking) {
        this._data.booking = booking;
        // if space selected no longer exists on booking change, pick first one.
        if (this._data.booking.room?.id && this._data.booking.room.id !== this._data.space?.id) {
            this._data.space = this._data.booking.room;
        }
    }

    /** Space to display booking details for */
    public get space(): Space {
        return this._data.space;
    }

    /** Type of meeting */
    public get type(): string {
        return this.booking.getType();
    }

    constructor(
        private _service: ApplicationService,
        private _bookings: BookingsService,
        private _booking_state_service: BookingStateService,
        private _dialog_ref: MatDialogRef<MeetingDetailsModalComponent>,
        private _dialog: MatDialog,
        @Inject(MAT_DIALOG_DATA) private _data: MeetingDetailsModalData,
    ) {
        super();
    }

    public ngOnInit(): void {
        if (!this.booking) {
            this._dialog_ref.close();
            return;
        };
    }

    public setLoading(state: string) {
        this.loading = state;
        this._dialog_ref.disableClose = !!this.loading;
    }

    public acceptMeeting() {
        this.setLoading('Accepting meeting...');
        const event = this.booking.toJSON();
        this._bookings
            .accept(this.booking.id, {
                concierge: true,
                host: this.booking.organiser.email,
                room_email: this.space.email,
                icaluid: this.booking.icaluid,
                start: event.start,
                end: event.end,
            })
            .then(() => {
                this._service.notifySuccess('Meeting approved.');
                this.booking.status = BOOKING_STATUS.approved;
                this._booking_state_service.replace(this.booking);
                this.setLoading(null);
                this.event.emit({ reason: 'action', metadata: BOOKING_STATUS.approved });
            }, this._on_error('Apologies, Unable to approve! This time slot has been booked recently by another user, please try booking another one.'));
    }

    public declineMeeting() {
        this.setLoading('Declining meeting...');
        this._bookings
            .decline(this.booking.id, {
                booking_id: this.booking.id,
                organiser: this.booking.organiser.email,
                icaluid: this.booking.icaluid,
                start: this.booking.startDateTz.seconds,
                end: this.booking.endDateTz.seconds,
                room_email: this.space.email,
            })
            .then(() => {
                this._service.notifySuccess('Meeting declined.');
                this.booking.status = BOOKING_STATUS.declined;
                this._booking_state_service.replace(this.booking);
                this.setLoading(null);
                this.event.emit({ reason: 'action', metadata: BOOKING_STATUS.declined });
            }, this._on_error('Unable to declining meeting, please try another one.'));
    }

    /** Open modal to confirm deletion of booking */
    public confirmDeleteMeeting() {
        const ref = this._dialog.open<DeleteConfirmModalComponent>(DeleteConfirmModalComponent);
        this.subscription(
            'confirm',
            ref.componentInstance.event.subscribe(async (event: DialogEvent) => {
                /* istanbul ignore else */
                if (event.reason === 'action') {
                    ref.componentInstance.loading = true;
                    const success = await this.deleteMeeting().catch(
                        this._on_error('Error deleting booking'),
                    );
                    ref.componentInstance.loading = false;
                    ref.componentInstance.success = !!success;
                    setTimeout(() => {
                        ref.close()
                    }, 3000);
                }
            }),
        );
    }

    /** Open model to cloning a booking */
    public cloningMeeting():void {
        this._dialog.open(BookingModalComponent, {
            data: {
                booking: this.booking,
                action: BookingAction.CLONE
            },
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop'
        });
    }

    /** Delete booking */
    private async deleteMeeting(opts: { series?: boolean } = {}) {
        this.setLoading('Deleting meeting...');
        await this.booking.delete(opts);
        this._service.notifySuccess('Successfully deleted booking');
        this.event.emit({ reason: 'action', metadata: 'removed' });
        this._dialog_ref.afterClosed().subscribe(_ => this.setLoading(null));
        this._dialog_ref.close();
        this._service.get('undo').next({ action: opts && opts.series ? 'series' : 'booking', booking: this.booking });
        return true;
    }

    /** Update booking requirements */
    public async updateRequirements(form: FormGroup) {
        this.setLoading('Updating requirements...');
        const value = form.value;
        const booking_data = this.booking.toJSON();
        const booking = new Booking({
            ...booking_data,
            ...value,
            room: value.space_list[0], // R--- not ideal
        });
         // if notes or catering exist? add 15 min buffer setup and breakdown to booking
         const has_note_buffer = (booking.notes && booking.notes?.length) || (booking.catering && booking.catering?.length) ? {[booking?.space?.email]: 900} :  null; // 900 seconds = 15 min
         if (has_note_buffer) {
            booking.setup = has_note_buffer;
            booking.breakdown = has_note_buffer;      
         }
        const new_booking: Booking = (await this._bookings
            .save(booking)
            .catch(this._on_error(`Error updating requirements`))) as any;
        if (new_booking) {
            const bkn = new Booking({ ...booking_data, ...new_booking.toApiJSON() });
            this._data.booking = bkn;
            this._booking_state_service.replace(bkn);
            this._service.notifySuccess('Successfully updated requirements');
        }
        this.setLoading(null);
    }

    public async updateBooking(form) {
        this.setLoading('Updating booking...');
        const value = form.value;
        const booking_data = this.booking.toJSON();
        const startDateTz = form.controls.dateTz.value
        const endDateTz = startDateTz.addValue({ minutes: value.duration })
        const booking = new Booking({
            ...booking_data,
            ...value,
            startDateTz,
            endDateTz,
            start_epoch: startDateTz.seconds,
            end_epoch: endDateTz.seconds,
            room: value.space_list[0], // R--- not ideal
        });
         // if notes or catering exist? add 15 min buffer setup and breakdown to booking
         const has_note_buffer = (booking.notes && booking.notes?.length) || (booking.catering && booking.catering?.length) ? {[booking?.space?.email]: 900} :  null; // 900 seconds = 15 min
         if (has_note_buffer) {
            booking.setup = has_note_buffer;
            booking.breakdown = has_note_buffer;      
         }
        const new_booking: Booking = (await this._bookings
            .save(booking)
            .catch(this._on_error(`Error updating booking`))) as any;
        if (new_booking) {
            this._booking_state_service.bookings.subscribe(res =>{
                const booking = res.find(book => this.booking.id === book.id);
                this.booking = booking; // if space selected no longer exists on booking change, this will pick first one.
            })
            this._service.notifySuccess('Successfully updated booking');
        }
        this.setLoading(null);
    }

    public async updateBreakdown(event) {
        this.setLoading('Updating requirements...');
        const booking_data = this.booking.toJSON();
        const _booking = new Booking({
            ...booking_data,
            setup: event.setup,
            breakdown: event.breakdown,
            notes: event.notes,
        });
        const new_booking: Booking = (await this._bookings
            .save(_booking)
            .catch(this._on_error(`Error updating requirements`))) as any;
        if (new_booking) {
            const bkn = new Booking({ ...booking_data, ...new_booking.toApiJSON() });
            this._data.booking = bkn;
            this._booking_state_service.replace(bkn);
            this._service.notifySuccess('Successfully updated requirements');
        }
        this.setLoading(null);
    }
}
