import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { combineLatest, Subject } from 'rxjs';
import { filter, first, map } from 'rxjs/operators';

import { CateringOrder } from '@mckinsey-converge/data-common';
import { BaseDirective } from '@mckinsey-converge/base';
import { Building } from '@mckinsey-converge/data-common';
import { Booking } from '@mckinsey-converge/data-common';
import { CateringCategory } from '@mckinsey-converge/data-common';
import { OrderDetailsModalComponent } from '../overlays/order-details-modal/order-details-modal.component';
import { SpacesService } from '@mckinsey-converge/data-common';
import { OrganisationService } from '@mckinsey-converge/data-common';
import { BookingStateService } from '@mckinsey-converge/data-common';

import { DateNow, DateTZ } from '@mckinsey-converge/date-tz';


@Component({
    selector: 'a-catering-orders',
    templateUrl: './orders.component.html',
    styleUrls: ['./orders.component.scss'],
})
export class CateringOrdersComponent extends BaseDirective implements OnInit, OnChanges {
    /** Search filter string */
    @Input() public search: string = '';
    /** Currently selected date */
    @Input() public date: number;
    /** Type of order to show */
    @Input() public type: '' | 'Pantry' | 'Kitchen';
    @Input() public orderDetailsExpanded: boolean;
    /** Subject holding the value of the search */
    public readonly search$ = new Subject<string>();
    /** List of catering orders for the selected date */
    public orders = this._booking_state_service.filtered.pipe(
        map((bookings) => {
            let list: CateringOrder[] = [];
            bookings.forEach((booking, i) => {
                const catering = [...booking.catering]
                    .map((order) => new CateringOrder({
                        ...order,
                        booking_date: booking.date,
                        booking
                    }));
                list = list.concat(catering);
            });
            list.sort((a, b) => a.booking_date - b.booking_date);
            return list;
        })
    );
    /** List of catering orders for the selected date */
    public filtered_orders = combineLatest([this.search$, this.orders]).pipe(
        map(([search, orders]) => this.filter(orders || [], search || ''))
    );

    public display_orders: CateringOrder[] = [];
    /** Whether catering orders are loading */
    public readonly loading = this._booking_state_service.loading;

    /** Display string for the currenly selected date */
    public get date_display(): string {
        // R--- check if input can be changed since there is isSameDate calculations
        const date = new DateTZ({date: this.date});
        const now = DateNow(new Date())
        return now.isSameDate(date, 'day') ? 'Today' : date.formatDate('dd MMM yyyy');
    }

    /** Active building */
    public get building(): Building {
        return this._org.building;
    }

    constructor(
        private _spaces: SpacesService,
        private _booking_state_service: BookingStateService,
        private _org: OrganisationService,
        private _route: ActivatedRoute,
        private _dialog: MatDialog
    ) {
        super();
    }

    public ngOnInit(): void {
        this._spaces.initialised.pipe(first((_) => _)).subscribe(() => {
            this._booking_state_service.startPollingEveryFiveMin();
            this.subscription(
                'building',
                this._org
                    .listen('active_building')
                    .pipe(filter((bld) => !!bld))
                    .subscribe(() => {
                        this._booking_state_service.setZone(this._org.building.id);
                    })
            );
            this.subscription(
                'orders',
                this.filtered_orders.subscribe((orders) => {
                    this.display_orders = orders.sort((a ,b) => {
                        const date = new DateTZ({date: a.booking.date}).formatDate('yyyy/MM/dd');

                        return Date.parse(`${date} ${a.deliver_at_time.slice(0, -2)} ${a.deliver_at_time.slice(-2)}`) - Date.parse(`${date} ${b.deliver_at_time.slice(0, -2)} ${b.deliver_at_time.slice(-2)}`)
                    }
                )})
            )
            this.subscription(
                'route.query',
                this._route.queryParamMap.subscribe((params) => {
                    /* istanbul ignore else */
                    if (params.has('view')) {
                        const view_id = params.get('view');
                        const details = localStorage.getItem('CONCIERGE.view_order');
                        const booking_details = localStorage.getItem('CONCIERGE.view_booking');
                        /* istanbul ignore else */
                        if (details) {
                            const booking = new Booking(JSON.parse(booking_details));
                            const order = new CateringOrder(JSON.parse(details));
                            order.booking = booking;
                            this.viewOrderDetails(order);
                            localStorage.removeItem('CONCIERGE.view_order');
                            localStorage.removeItem('CONCIERGE.view_booking');
                        }
                    }
                })
            );
            this.search$.next(this.search);
        });
    }

    public ngOnChanges(changes: SimpleChanges) {
        /* istanbul ignore else */
        if (changes.search || changes.type) {
            this.search$.next(this.search);
        }
    }

    /* istanbul ignore next */
    public trackByFn(index: number, order: CateringOrder) {
        return order ? order.id : index;
    }

    /** Open modal to view catering order details */
    public viewOrderDetails(order: CateringOrder): void {
        this._dialog.open(OrderDetailsModalComponent, {
            maxHeight: '95vh',
            maxWidth: '95vw',
            data: {
                order: order,
                booking: order.booking
            },
            autoFocus: false,
            backdropClass: 'mck-modal-backdrop',
        });
    }

    /**
     * Filter list to find matching items
     * @param list List of orders to filter
     * @param search Filter string
     */
    private filter(list: CateringOrder[], search: string = '') {
        const filter = search.toLowerCase();
        return list.filter((order) => {
            return (
                (!this.type ||
                    (this.type === 'Pantry' && order.pantry) ||
                    (this.type === 'Kitchen' && order.kitchen)) &&
                order.items.find(
                    (item: CateringCategory) =>
                        item.name.toLowerCase().indexOf(filter) >= 0 ||
                        (item.items &&
                            item.items.find((sub_item: CateringCategory) => {
                                return (
                                    sub_item.name.toLowerCase().indexOf(filter) >= 0 ||
                                    (sub_item.items &&
                                        sub_item.items.find((sub_sub_item) => {
                                            return (
                                                sub_sub_item.name.toLowerCase().indexOf(filter) >= 0
                                            );
                                        }))
                                );
                            }))
                )
            );
        });
    }
}
